import StoreStage from '@/components/main/StoreStage.vue';
export default {
  components: {
    StoreStage: StoreStage
  },
  data: function data() {
    return {
      stageConfig: {
        type: 'data',
        title: '我的数据源',
        tools: {
          build: {
            show: true
          },
          copy: {
            show: true
          }
        },
        files: [],
        style: {
          title: {
            backgroundColor: 'rgb(226, 200, 152)'
          }
        }
      }
    };
  },
  mounted: function mounted() {
    var _this = this;

    this.$API.getTableList(function (message) {
      if (message.success) {
        _this.stageConfig.files = message.info;
      }
    });
  }
};